<template>
<div>
<!--  热力分公司查询样本-->
  {{this.tmpCaption}}
  {{this.pri_detail.treeNodeOrgId}}
  <el-button style="float:right;right:100px;" type="primary" @click="showCb">检测</el-button>
<!--  <community-builder-set-info ref="cb"></community-builder-set-info>-->
</div>
</template>

<script>
// import CommunityBuilderSetInfo from "../../views/baseInfoManager/CommunityBuilderSetInfo";

export default {
  name: "Example6",
  // components:{CommunityBuilderSetInfo},
  mounted(){
    console.info("Mounted_Example6:"+this.pri_detail.treeNodeOrgId);
  },
  created(){
    console.info("Created_Example6:"+this.pri_detail.treeNodeOrgId);
    this.getQueryDetail();
  },
  props: {

    pri_detail: {
      type: Object,
      default: function () {
        return {
          treeNodeCaptions:[],
          treeNodeIds:[],
          treeNodeOrgId:'',

        }

      }
    }
  },
  watch:{
    pri_detail:function(newVal,oldVal){
      console.info("watch_Example6点击树节点查询【热力分公司】新值:"+newVal.treeNodeOrgId+",旧值："+oldVal.treeNodeOrgId);
      if(newVal.treeNodeOrgId!==oldVal.treeNodeOrgId){

        //查询
        this.tmpCaption = [];
        for(var i1=0;i1< this.pri_detail.treeNodeCaptions.length;i1++){
          this.tmpCaption[i1]= this.customGlobal.decrypt(this.pri_detail.treeNodeCaptions[i1],this.customGlobal.cryptoKey);
        }
        console.info(this.tmpCaption);
      }
    }
  },
  data(){
    return{
      tmpCaption:[],
    }
  },
  methods:{
    showCb(){
      // console.info("返回对象："+JSON.stringify(  this.$refs.cb.showPublicInfo() ));
      //this.$emit("parent_method","");
      let data = {};
      this.$emit("parent_method", "", val => {
           console.info("showCb_obj:"+ JSON.stringify(val) );
           data = val;
        }
      );

    },
    getQueryDetail: function(){
      if(this.pri_detail.treeNodeOrgId!==undefined){
        console.info("点击Tab标签页Example6查询【热力分公司】,获取值:"+this.pri_detail.treeNodeOrgId);

        //查询
        this.tmpCaption = [];
        for(var i1=0;i1< this.pri_detail.treeNodeCaptions.length;i1++){
          this.tmpCaption[i1]= this.customGlobal.decrypt(this.pri_detail.treeNodeCaptions[i1],this.customGlobal.cryptoKey);
        }
        console.info(this.tmpCaption);

      }else{
        console.info("当前没有选择任何组织机构ID，查询所有热力分公司");
      }
    }
  }
}
</script>

<style scoped>

</style>



